.hljs{display:block;overflow-x:auto;padding:.5em;background:#222;color:#fff;font-family:Menlo,Monaco,'Andale Mono','Lucida Console','Courier New',monospace;-webkit-text-size-adjust:none}
.hljs-comment,.hljs-title{color:#777}
.hljs-attribute,.hljs-name,.hljs-regexp,.hljs-tag,.hljs-variable,.html .doctype,.ruby .constant,.xml .doctype,.xml .pi,.xml .tag .title{color:#ab875d}
.css .value{color:#cd6a51}
.css .value .function,.css .value .string{color:#a67f59}
.css .value .number{color:#9b869c}
.css .class,.css .id,.css .selector,.css .tag,.css-pseudo{color:#dfc48c}
.hljs-built_in,.hljs-constant,.hljs-literal,.hljs-number,.hljs-params,.hljs-preprocessor{color:#ab875d}
.css .rules .attribute,.ruby .class .title{color:#9b869b}
.hljs-header,.hljs-inheritance,.hljs-string,.hljs-value,.ruby .symbol,.xml .cdata{color:#8f9c6c}
.css .hexcolor{color:#cd6a51}
.coffeescript .title,.function,.javascript .title,.perl .sub,.python .decorator,.python .title,.ruby .function .title,.ruby .title .keyword{color:#fff}
.hljs-keyword,.javascript .function{color:#8f9c6c}